#colaborador{
    display:block!important;
}

.blocks-color .special-block{
    padding: 120px 0 90px 0;
    background: url(bubbles-blanco.png) repeat-x #B4B0A7;
    position:relative;
    h2{
        padding:0 175px;
        font-size:1.5rem;
        line-height:2.2rem;
        text-align:center;
        color:#424242;
        margin-bottom:60px;
    }
    &:after {
        content: "";
        background: url(bubbles-claro.png) repeat-x;
        position: absolute;
        bottom: -44px;
        width: 100%;
        height: 45px;
        left:0;
    }
    .btn-enviar{
        background-color: #E65A2B;
        max-width: 20rem;
        margin: auto;
        padding: 1.5rem 0;
        display: none;
        text-align: center;
        font-size: 1.25rem;
        border-bottom: 0.25rem solid #AD4420;
        border-radius: 0.25rem;
        display: block;
    }
}

.box-info-no-image{
    margin:3rem auto 5rem auto;
}

.block-cards{
    margin:5rem 0;
    .container{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        .item{
            width:340px;
            height:300px;
        }
    }
}